html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
}

h1, h2, h3, h4, h5 {
  color: #00252e;
  margin: 0;
  font-family: "Poppins", sans-serif;
}

a, a:visited, a:active {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

button {
  cursor: pointer;
  font-family: "Open Sans", sans-serif;
  border: none;
  border-radius: 2rem;
}

li {
  list-style: none;
}

.header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 5rem;
}
.header__logo {
  width: 30%;
}
.header__logo img {
  width: 100%;
}
.header button {
  background-color: white;
  padding: 0.25rem 1.4375rem 0.375rem 1.375rem;
  border-radius: 2rem;
  font-size: 0.625rem;
  border: none;
  font-weight: bold;
}
.header button:hover {
  color: #808d99;
}

.header_hero_container {
  padding: 1.6875rem 1rem 2.3125rem 1rem;
  background-image: url(/images/bg-hero-mobile.svg);
  background-color: #ebfbff;
}

.hero {
  text-align: center;
  height: 100%;
  padding: 0 1.1875rem 0 1.1875rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.hero__text {
  text-align: left;
}
.hero__text h1 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}
.hero__text p {
  font-size: 1rem;
  color: #00252e;
  line-height: 1.5rem;
  margin-bottom: 2rem;
}
.hero__text button {
  background: #ff52bf;
  color: white;
  width: 15rem;
  height: 2.5rem;
  margin-bottom: 3.5rem;
}
.hero__text button:hover {
  background: #ff99db;
}
.hero__image img {
  width: 100%;
}

@media screen and (min-width: 50em) {
  .hero {
    flex-direction: row;
  }
  .hero__text {
    margin-right: 3.375rem;
  }
  .hero__text h1 {
    font-size: 2.5rem;
  }
  .hero__text p {
    font-size: 1.125rem;
  }
}
.about {
  text-align: center;
  padding: 3.6875rem 1.25rem 14.3125rem 1.25rem;
}
.about__item {
  box-shadow: 0px 0px 14px rgba(0, 0, 0, 0.07);
  padding: 0 2.5rem 0 2.5rem;
  border-radius: 1rem;
  height: 30.5rem;
}
.about__item img {
  width: 100%;
}
.about__item h3 {
  font-size: 1.25rem;
  margin-bottom: 1rem;
}
.about__item p {
  color: #808d99;
  font-size: 0.875rem;
  line-height: 1.3125rem;
}
.about__item:not(:last-child) {
  margin-bottom: 2.5rem;
}
.about .grow {
  padding-top: 3.5rem;
  padding-bottom: 2.0625rem;
}
.about .grow img {
  margin-bottom: 3.375rem;
  width: 93%;
}
.about .flow {
  padding-top: 4.1rem;
  padding-bottom: 3.375rem;
}
.about .flow img {
  margin-bottom: 4.1rem;
}
.about .users {
  padding-top: 3.729rem;
  padding-bottom: 2.0625rem;
}
.about .users img {
  margin-bottom: 3.729rem;
  width: 95%;
}

.footer {
  background: #00252e;
  color: white;
  font-size: 0.875rem;
  padding: 8.6875rem 1rem 2.875rem 1.3125rem;
}
.footer__logo {
  margin-bottom: 1.5625rem;
}
.footer path {
  fill: white;
}
.footer small {
  text-align: center;
  display: block;
}
.footer__contact {
  margin-bottom: 2.5625rem;
}
.footer__contact li {
  display: flex;
  align-items: center;
}
.footer__contact li:not(:last-child) {
  margin-bottom: 1.5rem;
}
.footer__contact li img {
  margin-right: 1rem;
}
.footer__nav {
  margin-bottom: 1.5rem;
}
.footer__nav li:not(:last-child) {
  margin-bottom: 1rem;
}
.footer__nav li a:hover {
  border-bottom: 1px white solid;
}
.footer__socials {
  text-align: center;
  margin-bottom: 1.5rem;
}
.footer__socials ion-icon {
  font-size: 2rem;
}
.footer__socials ion-icon:not(:nth-of-type(2)) {
  margin-right: 0.709rem;
}
.footer__socials ion-icon:hover {
  color: #ff52bf;
}

.cta__footer_container {
  position: relative;
}

.cta {
  text-align: center;
  padding: 12px;
  z-index: 5;
  position: absolute;
  top: -7.5rem;
}
.cta__container {
  box-shadow: 0px 0px 14px rgba(0, 0, 0, 0.07);
  padding: 2.5625rem 0.75rem;
  border-radius: 1rem;
  background: white;
}
.cta__container h2 {
  font-size: 1.125rem;
  margin-bottom: 1.5rem;
  line-height: 1.875rem;
}
.cta__container button {
  background: #ff52bf;
  color: white;
  width: 12.5rem;
  height: 2.5rem;
}
.cta__container button:hover {
  background: #ff99db;
}